import React, { Fragment } from 'react';
import navBack from '../images/navbar-background.png';

const Navbar = () => {
  const languageList = (
    <Fragment>
      <li>
        <a href="#!" className="center-align">
          العربية
        </a>
      </li>
      <li className="divider"></li>
      <li>
        <a href="#!" className="center-align">
          English
        </a>
      </li>
    </Fragment>
  );

  return (
    <Fragment>
      <div
        style={{
          position: 'sticky',
          top: '0',
          zIndex: '100',
        }}
      >
        <nav style={{ backgroundImage: `url(${navBack})` }}>
          <div className="nav-wrapper">
            <a href="#!" data-target="mobile-demo" className="sidenav-trigger">
              <i className="material-icons">menu</i>
            </a>

            <ul className="left hide-on-med-and-down text-darken-2">
              <li>
                <a href="!#">اتصل بنا</a>
              </li>
              <li>
                <a href="!#">التدريب</a>
              </li>
              <li>
                <a href="!#">التحول الرقمي</a>
              </li>
              <li>
                <a href="!#">التعليم عن بُعد</a>
              </li>
              <li className="active">
                <a href="!#">الرئيسية</a>
              </li>
            </ul>
          </div>
        </nav>
      </div>

      {/* Dropdown Structure Start*/}
      {/* 1 */}
      <ul id="dropdown1" className="dropdown-content">
        {languageList}
      </ul>
      {/* 2 */}
      <ul id="dropdown2" className="dropdown-content">
        {languageList}
      </ul>
      {/* Dropdown Structure End*/}

      <ul className="sidenav" id="mobile-demo">
        <li className="active right-align">
          <a href="collapsible.html">الرئيسية</a>
        </li>
        <li className="right-align">
          <a href="http://google.com/">التعليم عن بُعد</a>
        </li>
        <li className="right-align">
          <a href="http://google.com/">التحول الرقمي</a>
        </li>
        <li className="right-align">
          <a href="http://google.com/">التدريب</a>
        </li>
        <li className="right-align">
          <a href="sass.html">اتصل بنا</a>
        </li>
      </ul>
    </Fragment>
  );
};

export default Navbar;
