import React, { useState, useEffect } from 'react';
import { carousel } from '../../images/imagesIndex';
import './Carousel.css';

let interval;
const slides = document.getElementsByClassName('mySlides');

const Carousel = () => {
  const [index, setIndex] = useState(1);
  // NEXT SLIDE
  const nextSlide = () => {
    clearInterval(interval);
    setIndex((prevState) => {
      if (prevState < slides.length) {
        return prevState + 1;
      } else {
        return 1;
      }
    });
  };

  // PREVIOUS SLIDE
  const prevSlide = () => {
    clearInterval(interval);
    setIndex((prevState) => {
      if (prevState > 1) {
        return prevState - 1;
      } else {
        return slides.length;
      }
    });
  };
  const goToSlide = (n) => {
    clearInterval(interval);
    setIndex(n);
  };

  // AUTO FORWARD SLIDES
  useEffect(() => {
    slides[index - 1].style.display = 'block';
    interval = setInterval(() => {
      setIndex((prevState) => {
        if (prevState < slides.length) {
          return prevState + 1;
        } else {
          return 1;
        }
      });
    }, 3000);
    return () => {
      clearInterval(interval);
      slides[index - 1].style.display = 'none';
    };
  }, [index]);

  return (
    <>
      <div className="slideshow-container">
        <div className="mySlides fade">
          <img src={carousel[0]} style={{ width: '100%' }} alt="aaa" />
          <div className="text">Caption One, index is {index}</div>
        </div>
        <div className="mySlides fade">
          <img src={carousel[1]} style={{ width: '100%' }} alt="" />
          <div className="text">Caption Two, index is {index}</div>
        </div>
        <div className="mySlides fade">
          <img src={carousel[2]} style={{ width: '100%' }} alt="" />
          <div className="text">Caption Three, index is {index}</div>
        </div>

        {/* Next & Previous Manual Change */}
        <a className="prev" onClick={prevSlide} href="#!">
          &#10094;
        </a>
        <a className="next" onClick={nextSlide} href="#!">
          &#10095;
        </a>
      </div>

      {/* <!-- The dots/circles --> */}
      <div style={{ textAlign: 'center' }}>
        <span className="dot" onClick={() => goToSlide(1)}></span>
        <span className="dot" onClick={() => goToSlide(2)}></span>
        <span className="dot" onClick={() => goToSlide(3)}></span>
      </div>
    </>
  );
};

export default Carousel;
